















































































































import API from '@/utils/api'
import wxParse from '../../../components/mpvue-wxparse/wxParse.vue'
export default {
  data() {
    return {
      loaded: false,
      isPlay: false,
      audioContext: null,
      body: '',
      title: ''
    }
  },
  components: {
    wxParse
  },
  onLoad(query) {
    const id = query.id
    if (!id) console.log('error coured no id find.')
    this.getDetail(id)
  },
  methods: {
    async getDetail(id) {
      const data = await API.getZhDtl(id)
      this.body = data.body
      this.title = data.title
    }
  }
}
